/*------------------------------------*\
  HEADER
\*------------------------------------*/
header {
  position: relative;
  background-color: #fff;
  z-index: 1000; }

.header-container {
  height: 72px;
  padding-top: 25px;
  padding-bottom: 25px;
  border-bottom: 1px solid #EBEFEE; }

.header-logo {
  position: relative;
  display: inline-block;
  float: left;
  width: 200px;
  height: 72px; }
  .header-logo a {
    margin-right: 20px; }
  .header-logo p {
    display: inline-block;
    position: absolute;
    top: 50%;
    -webkit-transform: translateY(-50%);
    -ms-transform: translateY(-50%);
    transform: translateY(-50%);
    font-size: 18px;
    font-weight: 700; }

.header-menu {
  position: relative;
  display: inline-block;
  float: right;
  width: 65px;
  height: 72px; }

.button_container {
  position: relative;
  left: 15px;
  top: 18px;
  height: 35px;
  width: 35px;
  cursor: pointer;
  z-index: 100;
  transition: opacity .25s ease; }

.button_container:hover {
  opacity: .7; }

.button_container.active .top {
  transform: translateY(16px) translateX(0) rotate(45deg);
  background: #FFF; }

.button_container.active .middle {
  opacity: 0;
  background: #FFF; }

.button_container.active .bottom {
  transform: translateY(-14px) translateX(0) rotate(-45deg);
  background: #FFF; }

.button_container span {
  background-color: #2F333C;
  border: none;
  height: 5px;
  width: 100%;
  position: absolute;
  top: 0;
  right: 0;
  transition: all .35s ease;
  cursor: pointer; }

.button_container span:nth-of-type(2) {
  top: 15px; }

.button_container span:nth-of-type(3) {
  top: 30px; }

.overlay {
  position: fixed;
  background: #1DA2DD;
  top: 0;
  left: 0;
  width: 100%;
  height: 100%;
  opacity: 0;
  visibility: hidden;
  transition: opacity .35s, visibility .35s, height .35s;
  overflow: hidden; }

.overlay.open {
  opacity: 1;
  visibility: visible;
  height: 100%; }

.overlay.open li {
  animation: fadeInRight .5s ease forwards;
  animation-delay: .35s; }

.overlay.open li:nth-of-type(2) {
  animation-delay: .4s; }

.overlay.open li:nth-of-type(3) {
  animation-delay: .45s; }

.overlay.open li:nth-of-type(4) {
  animation-delay: .50s; }

.overlay nav {
  position: relative;
  height: 75%;
  top: 50%;
  transform: translateY(-50%);
  font-size: 26px;
  font-weight: 700;
  text-align: center; }

.overlay ul {
  list-style: none;
  padding: 0;
  margin: 0 auto;
  display: inline-block;
  position: relative;
  height: 100%; }

.overlay ul li {
  display: block;
  height: 25%;
  height: calc(100% / 7);
  position: relative;
  opacity: 0; }

.overlay ul li a {
  display: block;
  position: relative;
  color: #FFF;
  text-decoration: none;
  overflow: hidden; }

.overlay ul li a:hover:after, .overlay ul li a:focus:after, .overlay ul li a:active:after {
  width: 100%; }

.overlay ul li a:after {
  content: '';
  position: absolute;
  bottom: 0;
  left: 50%;
  width: 0%;
  transform: translateX(-50%);
  height: 3px;
  background: #FFF;
  transition: .35s; }

.hidden {
  overflow: hidden; }

@keyframes fadeInRight {
  0% {
    opacity: 0;
    left: 20%; }
  100% {
    opacity: 1;
    left: 0; } }

