/*------------------------------------*\
  HEADER
\*------------------------------------*/

header {
	position: relative;
	background-color: #fff;
	z-index: 1000;
}

.header-container {
	height: 72px;
	padding-top: 25px;
	padding-bottom: 25px;
	border-bottom: 1px solid #EBEFEE;
}

.header-logo {
	position: relative;
	display: inline-block;
	float: left;
	width: 200px;
	height: 72px;
	a {
		margin-right: 20px;
	}
	p {
		display: inline-block;
		@include center (vertical);
		font-size: $fz + 4;
		font-weight: 700;
	}
}

.header-menu {
	position: relative;
	display: inline-block;
	float: right;
	width: 65px;
	height: 72px;
}

.button_container {
	position: relative;
	left: 15px;
	top: 18px;
	height: 35px;
	width: 35px;
	cursor: pointer;
	z-index: 100;
	transition: opacity .25s ease;
}

.button_container:hover {
	opacity: .7;
}

.button_container.active .top {
	transform: translateY(16px) translateX(0) rotate(45deg);
	background: #FFF;
}

.button_container.active .middle {
	opacity: 0;
	background: #FFF;
  }

.button_container.active .bottom {
	transform: translateY(-14px) translateX(0) rotate(-45deg);
	background: #FFF;
}

.button_container span {
	background-color: $dark;
	border: none;
	height: 5px;
	width: 100%;
	position: absolute;
	top: 0;
	right: 0;
	transition: all .35s ease;
	cursor: pointer;
}

.button_container span:nth-of-type(2) {
	top: 15px;
}

.button_container span:nth-of-type(3) {
	top: 30px;
}
  
.overlay {
	position: fixed;
	background: $blue;
	top: 0;
	left: 0;
	width: 100%;
	height: 100%;
	opacity: 0;
	visibility: hidden;
	transition: opacity .35s, visibility .35s, height .35s;
	overflow: hidden;
}

.overlay.open {
	opacity: 1;
	visibility: visible;
	height: 100%;
}

.overlay.open li {
	animation: fadeInRight .5s ease forwards;
	animation-delay: .35s;
}

.overlay.open li:nth-of-type(2) {
	animation-delay: .4s;
}

.overlay.open li:nth-of-type(3) {
	animation-delay: .45s;
}

.overlay.open li:nth-of-type(4) {
	animation-delay: .50s;
}

.overlay nav {
	position: relative;
	height: 75%;
	top: 50%;
	transform: translateY(-50%);
	font-size: 26px;
	font-weight: 700;
	text-align: center;
}

.overlay ul {
	list-style: none;
	padding: 0;
	margin: 0 auto;
	display: inline-block;
	position: relative;
	height: 100%;
}

.overlay ul li {
	display: block;
	height: 25%;
	height: calc(100% / 7);
	position: relative;
	opacity: 0;
}

.overlay ul li a {
	display: block;
	position: relative;
	color: #FFF;
	text-decoration: none;
	overflow: hidden;
}

.overlay ul li a:hover:after, .overlay ul li a:focus:after, .overlay ul li a:active:after {
	width: 100%;
}

.overlay ul li a:after {
	content: '';
	position: absolute;
	bottom: 0;
	left: 50%;
	width: 0%;
	transform: translateX(-50%);
	height: 3px;
	background: #FFF;
	transition: .35s;
}


.hidden {
	overflow: hidden;
}
  
@keyframes fadeInRight {
	0% {
	  opacity: 0;
	  left: 20%;
	}
	100% {
	  opacity: 1;
	  left: 0;
	}
}

/*Adaptive ----------------------- */

@media (max-width: 1200px) {

	.header-container {
		padding-top: 0px;
		padding-bottom: 0px;
	}

	.header-logo {
		img {
			width: 40px;
			margin-top: 15px;
		}
	}

	.button_container {
		margin-top: 5px;
		span {
			width: 30px;
			height: 4px;

		}
	}

	.button_container span:nth-of-type(2) {
		top: 10px;
	}

	.button_container span:nth-of-type(3) {
		top: 20px;
	}

	.button_container.active .top {
		transform: translateY(10px) translateX(0) rotate(45deg);
		background: #FFF;
	}
	
	.button_container.active .middle {
		opacity: 0;
		background: #FFF;
	  }
	
	.button_container.active .bottom {
		transform: translateY(-10px) translateX(0) rotate(-45deg);
		background: #FFF;
	}


}


@media (max-width: 320px) {

	.header-logo {
		width: 68px;
		p {
			display: none;
		}
	}

}




/*------------------------------------*\
  FIRST SCREEN
\*------------------------------------*/

.first-container {
	padding-top: 190px;
	padding-bottom: 120px;
}

.first-screen {
	background-image: url('../img/header-dot.jpg');
	background-repeat: no-repeat;
	background-position-x: right;
	background-position-y: 400px;
}

.first-screen__left {
	display: inline-block;
	float: left;	
}

.left__title {
	position: relative;
	padding-left: 30px;
	margin-top: 110px;
	margin-bottom: 70px;
	h1 {
		text-transform: uppercase;
		font-weight: 900;
		line-height: 60px;
		margin-bottom: 15px;
	}
	p {
		font-size: $fz + 12;
		font-weight: 900;
		line-height: 26px;
	}
	&:before {
		position: absolute;
		content: '';
		top: 0;
		left: 0;
		width: 5px;
		height: 100%;
		background-color: $blue;
	}
}

.left__text {
	width: 460px;
	font-size: $fz + 2;
	margin-bottom: 50px;
}

.left__buttons {
	a {
		margin-left: 30px;
		font-size: $fz + 2;
		font-weight: 700;
		text-transform: uppercase;
		text-decoration: none;
		color: $dark;
		transition-duration: 0.2s;
		svg {
			position: relative;
			top: 4px;
			left: 5px;
		}
		&:hover {
			color: $blue;
		}
	}
}

.main-btn {
	width: 180px;
	height: 50px;
	border-radius: 50px;
	font-size: $fz + 2;
	font-weight: 700;
	text-transform: uppercase;
	border: none;
	background-color: $blue;
	color: #fff;
	transition-duration: 0.2s;
	&:hover {
		cursor: pointer;
		background-color: #26B4F0;
	}
}

.first-screen__right {
	display: inline-block;
	float: right;
}


/*Adaptive ----------------------- */

@media (max-width: 1200px) {

	.first-screen {
		background-image: none;
	}

	.left__title {
		margin-top: 0;
		margin-bottom: 40px;
	}

	.first-screen__right img {
		width: 500px;
	}

}

@media (max-width: 992px) {

	.first-container {
		padding-top: 100px;
		padding-bottom: 100px;
	}

	.first-screen__left {
		margin-bottom: 20px;
	}
	
	.first-screen__right {
		position: absolute;
		right: 0;
		margin-top: 30px;
		img { 
			width: 300px;
		}
	}

}

@media (max-width: 768px) {

	.first-screen {
		background-position-y: 440px;
	}


	.first-container {
		padding-top: 60px;
		padding-bottom: 60px;
	}

	.first-screen__left {
		text-align: center;
		width: 100%;
		margin-bottom: 30px;
	}

	.first-screen__right {
		position: relative;
		width: 100%;
		height: 270px;
		margin-top: 0;
		img {
			@include center (horizontal);
		}
	}

	.left__title {
		padding-left: 0;
		&::before {
			display: none;
		}
		
	}

	.left__text {
		width: 100%;
		span {
			font-size: $fz + 6;
		}
	}

}

@media (max-width: 576px) {

	.first-screen {
		background-position-y: 460px;
	}
	
	.left__title {
		margin-bottom: 30px;
	}

	.left__text {
		span {
			font-size: $fz + 4;
		}
	}

	.left__buttons {
		button {
			width: 100%;
			font-size: $fz + 6;
			margin-bottom: 30px;
		}
	}

	.first-screen__right {
		height: 220px;
		img {
			width: 240px;
		}
	}

}

@media (max-width: 480px) {

	.first-screen {
		background-position-y: 480px;
	}

}

@media (max-width: 320px) {

	.first-screen {
		background-position-y: 450px;
	}

	.left__title {
		h1 {
			margin-bottom: 0;
		}
		p {
			font-size: $fz + 4;
		}
	}

}